<template>
  <el-card class="">
    <div slot="header">
      <span>权限设置</span>
    </div>
    <el-steps :active="current_step" finish-status="success" align-center>
      <el-step title="路由权限"></el-step>
      <el-step title="菜单、操作权限"></el-step>
    </el-steps>
    <!-- 第一步：设置路由 -->
    <div v-show="current_step === 0" class="route-panel">
      <el-tree :data="routes_tree" ref="route_tree" :check-strictly="route_check_strictly"
               show-checkbox highlight-current default-expand-all node-key="route_id">
        <div class="custom-tree-node" slot-scope="{ node, data }">
          <el-tooltip class="item" effect="dark" :content="data.route_name" placement="right">
            <span>{{ data.permission_remark }}</span>
          </el-tooltip>
        </div>
      </el-tree>
    </div>
    <!-- 第二步：设置菜单/操作按钮 -->
    <div class="menu-panel" v-show="current_step === 1">
      <el-tree :data="menus_tree" ref="menu_tree" :check-strictly="menu_check_strictly"
               show-checkbox highlight-current default-expand-all node-key="permission_id">
        <div class="custom-tree-node" slot-scope="{ node, data }">
          <!-- 菜单 -->
          <div class="node-menu" v-if="data.resource_type_id === 2">
            <i class="el-icon-notebook-2"></i>
            {{ data.menu_name }}
          </div>
          <!-- 操作按钮 -->
          <div class="node-operation" v-if="data.resource_type_id === 3">
            <i class="el-icon-s-operation"></i>
            {{ data.operation_name }}
          </div>
        </div>
      </el-tree>
    </div>
    <!-- 操作按钮 -->
    <div class="action-section am-padding-vertical">
      <!-- 第一步：设置路由 -->
      <el-button @click="handleStepNext" v-show="current_step === 0" type="primary" size="medium">下一步</el-button>
      <el-button @click="$router.back()" v-show="current_step === 0" plain size="medium">取消</el-button>
      <!-- 第二步：设置菜单/操作按钮 -->
      <el-button @click="handleStepPrev" v-show="current_step === 1" plain size="medium">上一步</el-button>
      <el-button @click="handleSetAll" v-show="current_step === 1" type="success" size="medium">保存设置</el-button>
    </div>
  </el-card>
</template>

<script>
import { mapGetters } from 'vuex'
import { Role } from '@/api/'

export default {
  name: "Setting",
  props: ['role_id'],
  data() {
    return {
      // 步骤条
      current_step: 0,
      // 路由--是否严格的遵循父子不互相关联
      route_check_strictly: true,
      // 路由--选中的路由 permission_id 数组
      route_checked_keys: [],
      // 菜单--树形结构
      menus_tree: [],
      // 菜单--是否严格的遵循父子不互相关联
      menu_check_strictly: true,
    }
  },
  computed: {
    // 加载所有路由权限
    ...mapGetters('Route', ['routes_tree']),
    ...mapGetters('Menu', ['menu_from_checked_route']),
  },
  created() {
    // 设置 title
    document.title = '设置角色权限';
    // 还原以前选中的路由
    this.restoreCheckedRoute();
  },
  methods: {
    // 路由 -- 还原以前选中的状态
    async restoreCheckedRoute() {
      // 根据角色加载对应路由
      let { status, data } = await Role.route({ role_id: this.role_id, type: 'flat' });
      if (status) {
        // 转化为选中的路由id
        let checked_keys = data.map((route) => route.route_id);
        // 设置选中的路由
        this.$refs.route_tree.setCheckedKeys(checked_keys);
        // 还原选中状态之后，设置父子节点互相关联
        this.route_check_strictly = false;
      }
    },
    // 下一步操作
    handleStepNext() {
      // 获取当前选中的路由 node节点（包含半选中父级节点）
      let route_checked_nodes = this.$refs.route_tree.getCheckedNodes(false, true);
      // 转化为 permission_id 数组
      this.route_checked_keys = route_checked_nodes.map((route) => route.permission_id);
      // 上一步选中的路由，决定菜单/操作按钮的禁用状态，整合禁用的菜单/操作按钮，生成新的树形结构
      this.menus_tree = this.menu_from_checked_route(route_checked_nodes);
      // 还原选中的菜单/操作按钮
      this.restoreCheckedMenu(route_checked_nodes);
      // 切换步骤条
      this.current_step = 1;
    },
    // 返回上一步
    handleStepPrev() {
      // 菜单--是否严格的遵循父子不互相关联
      this.menu_check_strictly = true,
      // 切换步骤条
      this.current_step = 0
    },
    // 菜单/操作按钮 -- 上一步选中的路由，结合之前的选中的状态，综合计算选中数组
    async restoreCheckedMenu(route_checked_nodes) {
      // 根据角色id，获取选中的菜单/操作按钮
      let { status, data: menu_origin_checked } = await Role.menu({ role_id: this.role_id, type: 'flat' });
      if (status) {
        // 选中的菜单数组--permission_id
        let menu_checked_permission = [];
        // 判断当前菜单是否选中，选中，则添加其进入选中数组
        menu_origin_checked.forEach((origin) => {
          // 判断当前菜单是否被选中路由
          let isExist = route_checked_nodes.some((route) => route.route_id === origin.route_id);
          if (isExist) {
            menu_checked_permission.push(origin.permission_id);
            // 比对菜单子级的操作按钮是否选中
            let operations = menu_origin_checked.filter((operation) => {
              if (operation.resource_type_id === 3) {
                return operation.parent_id === origin.permission_id
              } else {
                return false;
              }
            });
            // 转化为 permission_id 数组
            let operation_permission = operations.map((operation) => operation.permission_id);
            // 添加当前操作按钮进入选中数组
            menu_checked_permission.push(...operation_permission);
          }
        });
        // 设置选中的菜单
        this.$refs.menu_tree.setCheckedKeys(menu_checked_permission);
        // 还原选中状态之后，设置父子节点互相关联
        this.menu_check_strictly = false;
      }
    },
    // 菜单/操作按钮 -- 计算选中的节点keys
    getCheckedMenu() {
      // 获取菜单、操作按钮当前选中的node节点
      let menu_checked_keys = this.$refs.menu_tree.getCheckedKeys();
      // 获取菜单、操作按钮当前半选中的父级节点
      let menu_half_checked_keys = this.$refs.menu_tree.getHalfCheckedKeys();
      // 合并数组，缓存
      return [...menu_checked_keys, ...menu_half_checked_keys];
    },
    // 保存全部设置
    async handleSetAll() {
      // 路由 -- 设置权限
      let { status: route_status, msg: route_msg } = await Role.set_route({ role_id: this.role_id, permissions: this.route_checked_keys });
      // 菜单/操作按钮 -- 获取选中的keys
      let permissions = this.getCheckedMenu();
      // 菜单/操作按钮 -- 设置权限
      let { status: menu_status, msg: menu_msg } = await Role.set_menu({ role_id: this.role_id, permissions });
      // 统一处理信息提示
      if (route_status && menu_status) {
        this.$message.success('权限设置成功！');
        this.$router.back();
      } else {
        this.$message.error(route_msg);
        this.$message.error(menu_msg);
      }
    },
  }
}
</script>

<style lang="less" scoped>
@blue: #409EFF;
@green: #67C23A;
@orange: #E6A23C;

.route-panel, .menu-panel {
  margin-top: 20px;
  padding: 20px;
  border-radius: 3px;
}

.route-panel {
  border: 1px solid @blue;
}

.menu-panel {
  border: 1px solid @green;

  .node-menu i {
    color: @green;
  }

  .node-operation i {
    color: @orange;
  }
}

.custom-tree-node {
  flex: 1;
  display: flex;
  align-items: center;
  font-size: 14px;
  padding-right: 8px;
}
</style>